.personnages {
    display: block;
  
    color: #ccc;
    background-color: rgb(50, 50, 50);
    border-color: white;
  
    text-align: center;
    text-decoration: none;
  
    margin-top: 10px;
    margin-bottom: 10px;
  
    line-height: 70px;
  
    transition: .2s;
}

.personnages:hover {
  background: rgb(75, 75, 75);
  cursor: pointer;
}

