.base-de-donnee ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    list-style: none;

    padding: 0 20px;
}

.base-de-donnee ul li {
    border: solid;
    border-radius: 10px;
    padding-left: 10px;
}

ul li.ajout {
    border: none;
}

.ajout-bouton {
    border-radius: 70px;
}